  padding: 0 15px;
  font-weight: bold;
  overflow: visible;
  // 
  .icon_container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 24px;
    height: 24px;
  }
  // 
  .ic-menu_item_txt {
    flex-grow: 1;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  // 
  .collapse_menu_item {
    position: absolute;
    left: 54px;
    top: 50%;
    transform: translate(0, -50%);
  }
}
</style>
