<template>
  <div class="public_avatar">
    <div v-if="isError">
      <!--  -->
      <img
        :src="defaultAvata"
        :style="{width, height: width}"
        class="avatar_img">
      <!--  -->
      <template v-if="isUpload">
        <div class="shadow"
          :style="{width, height: width}"
          @click="uploadFile">
          <img
            :src="avatarCamera"
            :style="{width: `${parseFloat(width) / 2}${company}`}"
            class="camera_img">
        </div>
        <!-- input -->
        <input
          ref="ic-file-input"
          style="display: none;"
          type="file"
          @change="fileChange">
      </template>
    </div>
    <div v-else class="public_error">
      
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
// 
import defaultAvata from '../image/default_user_avatar.png'
// 
import avatarCamera from '../image/avatar_camera.png'

export default {
  name: 'IcAvatar',
  props: {
    // 
    headerImg: {
      type: String,
      default: ''
    },
    // 
    width: {
      type: String,
      default: '40px'
    },
    // 
    isUpload: {
      type: Boolean,
      default: true
    },
    // 
    size: {
      type: [String, Number],
      default: 5
    }
  },
  data () {
    return {
      avatarCamera,
      defaultAvata,
      company: 'px', // 
      isError: true, // 
      imgType: ['png', 'jpg', 'jpeg'], // 
      lang: Cookies.get('lang') || 'zh',
      sizeTipsZh: '',
      sizeTipsEn: 'File cannot be greater than',
      formatTipsZh: ' "png", "jpg", "jpeg"',
      formatTipsEn: 'File format error, support "PNG", "JPG", "JPEG"'
    }
  },
  watch: {
    headerImg (newName) {
      this.defaultAvata = newName ? this.headerImg : defaultAvata
    },
    width (newName) {
      //  
      this.company = this.width.indexOf('rem') !== -1 ? 'rem' : 'px'

      // 
      this.isError = !(this.width.indexOf('rem') === -1 && this.width.indexOf('px') === -1)
      if (!this.isError) {
        // console.log('ic-avatar  width, ---- px/rem')
      }
    }
  },
  created () {
    this.defaultAvata = this.headerImg ? this.headerImg : defaultAvata
  },
  methods: {
    // 
    uploadFile () {
      const dom = this.$refs['ic-file-input']
      dom.value = null // input 
      dom.click()
    },

    // 
    async fileChange (e) {
      try {
        const file = e.target.files[0]
        const name = file.name
        const arr = name.split('.')
        const type = arr[arr.length - 1]
        const size = ((file.size / 1024) / 1024)
        if (type && this.imgType.indexOf(type) > -1) {
          if (size > parseInt(this.size)) {
            this.$message.error(this.lang === 'zh' ? `${this.sizeTipsZh} ${this.size}` : `${this.sizeTipsEn} ${this.size}M`)
            return
          }
          const formData = new FormData()
          formData.append('file', file)
          // 
          this.$emit('upload', formData)
        } else {
          this.$message.error(this.lang === 'zh' ? this.formatTipsZh : this.formatTipsEn)
        }
      } catch (e) {}
    }
  }
}
</script>

<style lang="scss">
  .public_avatar {
    position: relative;
    display: inline-block;
    &:hover {
      .shadow {
        visibility: unset;
      }
    }
    .avatar_img,
    .shadow {
      border-radius: 50%;
    }
    .shadow {
      visibility: hidden;
      cursor: pointer;
      user-select: none;
      position: absolute;
      left: 0;
      top: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgba(0, 0, 0, .5);
    }
    .public_error {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      border: 1px solid red;
      font-size: 14px;
      text-align: center;
      line-height: 40px;
      color: #000;
    }
  }
</style>
